import axios from 'axios';
import { createStore } from 'vuex'

const store = createStore({
  state(){
    return {
      name : 'kim',
      age : 20,
      likes : 0,
      좋아요체크 : false,
      more : {},
    }
  },
  //mutations 는 state정보를 변경시키는것
  mutations : {
      setMore(state, data){
        state.more = data
      },
      좋아요(state) {
        if (state.좋아요체크 == false) {
            state.likes++;
            state.좋아요체크 = true;
        } else {
            state.likes--;
            state.좋아요체크 = false;
        }
        
      },

      이름변경(state){
          state.name = 'park'
      },
      증가(state, data){
          state.age += data
      }
  },
  actions : {
    //ajax하는곳 또는 오래걸리는 작업들(예를 들어, 더보기 게시물 ajax요청을 해보겠음)
    getData(context){
        axios.get('https://codingapple1.github.io/vue/more0.json').then((a)=>{
            console.log(a.data);
            context.commit('setMore', a.data)
        })
    },
  },
})

export default store