<template>
  <div class="header">
    <ul class="header-button-left">
      <li>Cancel</li>
    </ul>
    <ul class="header-button-right">
      <li v-if="step == 1" @click="step++">Next</li>
      <li v-if="step == 2" @click="publish">발행</li>
    </ul>
    <img src="./assets/logo.png" class="logo" />
  </div>

  <p>{{내이름}} {{age}} {{likes}}</p>

  <Container :VuestaData="VuestaData" :step="step" :chooseFileURL="chooseFileURL" @contentFromChild="작성한글내용 = $event"/>
  <!-- <button @click="more">더보기</button> -->

 

  <div class="footer">
    <ul class="footer-button-plus">
      <input @change="upload" accept="image/*" type="file" id="file" class="inputfile" />
      <label for="file" class="input-plus">+</label>
    </ul>
  </div>

</template>

<script>
import Container from "./components/Container.vue";
import Vuesta from "./assets/Vuesta.js";
import {mapMutations, mapState} from 'vuex';
//import axios from "axios";


//컴포넌트, 데이터 많을때 Vuex쓰고 그 외엔 props쓰는게 더 짧음
// Vuex4 셋팅 -> 모든 컴포넌트가 쓸 수 있는 데이터 저장용 js파일 만들어야함

export default {
  name: "App",
  data() {
    return {
      step : 0,
      //idx: 0,
      VuestaData: Vuesta,
      chooseFileURL: '',
      작성한글내용: '',
      선택한필터 : '',
      카운터: 0,
    };
  },
  mounted() {
    //mitt 사용해서 하하위 컴포넌트에서 데이터 가져왔음
    this.emitter.on('박스클릭함', (a)=>{
      this.선택한필터 = a;
    });
  },  

  components: {
    Container: Container,
  },


  computed : {
    name(){
      return this.$store.state.name
    },
    ...mapState(['name', 'age', 'likes']),
    ...mapState({ 내이름 :'name', })
  },

  methods: {
    ...mapMutations(['setMore', '좋아요']),

    publish() {
      var 내게시물 = {
        name: "거누",
        userImage: "https://placeimg.com/100/100/arch",
        postImage: this.chooseFileURL,
        likes: 36,
        date: "May 15",
        liked: false,
        content: this.작성한글내용,
        filter: this.선택한필터
      };
      this.VuestaData.unshift(내게시물);  //unshift는 왼쪽의 array에 자료 집어 넣어줌
      this.step = 0;
    },
    // 더보기 버튼(Axios 사용)
    // more() {
    //   axios
    //     .get(`https://codingapple1.github.io/vue/more${this.idx}.json`)
    //     .then((결과) => {
    //       //then()은 get요청이 성공했을떄 수행하는 기능
    //       console.log(결과.data);
    //       this.VuestaData.push(결과.data);
    //       this.idx++;
    //     });
    // },
    upload(e){
      let 파일 = e.target.files;
      console.log(파일[0]);
      let url = URL.createObjectURL(파일[0]); //업로드한 파일의 임시URL, 
      console.log(url);
      this.chooseFileURL = url;
      this.step = 1;
    }
  },
};
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

@import "./assets/App.vue.css";
</style>
