<template>
  <div>
    <div v-if="step == 0">
      <Post :ChooseVuestaData="VuestaData[i]" v-for="(a, i) in VuestaData" :key="i"/>
      <!-- <Post :ChooseVuestaData="VuestaData[0]"/> -->
    </div>

    <!-- 필터선택페이지 -->
    <div v-if="step == 1">
      <div :class="선택한필터" class="upload-image" :style="{ backgroundImage: `url(${chooseFileURL})` }"></div>
      <div class="filters">
        <FilterBox :chooseFileURL="chooseFileURL" :필터="필터" v-for="필터 in 필터들" :key="필터">
            <span>{{필터}}</span>
        </FilterBox>
      </div>
    </div>

    <!-- 글작성페이지 -->
    <div v-if="step == 2">
      <div :class="선택한필터" class="upload-image" :style="{ backgroundImage: `url(${chooseFileURL})` }"></div>
      <div class="write">
        <textarea @input="$emit('contentFromChild', $event.target.value)" class="write-box">내용을 작성해주세요</textarea>
      </div>
    </div>
  </div>
</template>

<script>
import Post from "./Post.vue";
import FilterBox from "./FilterBox.vue";

export default {
    data(){
        return {
            필터들 : ["aden", "_1977", "brannan", "brooklyn", "clarendon", "earlybird", "gingham", "hudson", 
                    "inkwell", "kelvin", "lark", "lofi", "maven", "mayfair", "moon", "nashville", "perpetua", 
                    "reyes", "rise", "slumber", "stinson", "toaster", "valencia", "walden", "willow", "xpro2"],
            선택한필터: '',
        }
    },
    mounted() {
    //mitt 사용해서 하하위 컴포넌트에서 데이터 가져왔음
    this.emitter.on('박스클릭함', (a)=>{
      this.선택한필터 = a;
    });
  },

  name: "Container",
  components: {
    Post : Post,
    FilterBox : FilterBox,
  },
  props: {
    VuestaData: Array,
    step: Number,
    chooseFileURL: String,
  },
};
</script>

<style>
.upload-image {
  width: 100%;
  height: 450px;
  background: cornflowerblue;
  background-size: cover;
}
.filters {
  overflow-x: scroll;
  white-space: nowrap;
}
.filter-1 {
  width: 100px;
  height: 100px;
  background-color: cornflowerblue;
  margin: 10px 10px 10px auto;
  padding: 8px;
  display: inline-block;
  color: white;
  background-size: cover;
}
.filters::-webkit-scrollbar {
  height: 5px;
}
.filters::-webkit-scrollbar-track {
  background: #f1f1f1;
}
.filters::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 5px;
}
.filters::-webkit-scrollbar-thumb:hover {
  background: #555;
}
.write-box {
  border: none;
  width: 90%;
  height: 100px;
  padding: 15px;
  margin: auto;
  display: block;
  outline: none;
}
</style>