<template>
  <!-- <div :class="`${필터들[i]} filter-item`" :style=" { backgroundImage : `url(${chooseFileURL})`}" 
  v-for="(a,i) in 필터들" :key ="i"> -->
  <div :class="`${필터} filter-item`" :style=" { backgroundImage : `url(${chooseFileURL})`}" @click="fire">
    <slot></slot>     <!-- slot으로 부모 -> 자식 데이터전송 가능 -->
                      <!-- slot 사용할 때 부모가 자식데이터 필요한 경우 => slot props -->
  </div>
</template>

<script>
export default {
    name : 'filterbox',
   
    methods : {
        fire(){
        this.emitter.emit('박스클릭함', this.필터) //하위 컴포넌트에서 상위 상위 (그 이상도 가능)로 정보전달하는 기능
        console.log(this.필터);
        }
    },
    props: {
        chooseFileURL : String,
        필터 : String,
        
    },
};
</script>

<style>
.filter-item {
  width: 100px;
  height: 100px;
  margin: 10px 10px 10px auto;
  padding: 8px;
  display: inline-block;
  color: white;
  background-size: cover;
  background-position: center;
}
</style>